
/*--Universal Selector--*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/*--Element Selectors--*/
body {
  background-color: rgb(249, 213, 194);
}

a {
  color: rgb(241, 241, 232);
}

a:hover {
  color: gray;
}

/*--Header Nav and Main Image--*/
.nav {
  top: 0px;
  z-index: 1;
  padding: 20px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  background-color: #1e1e1e;
  color: rgb(241, 241, 232);
  width: 100%
}

.nav {
  display: inline-block;
  font-size: 20px;
}

.nav-item {
  padding-top: 10px;
  margin-right: 20px;
  float: right;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 20px;
}

.nav-tabs {
  list-style-type: none;
}

.nav {
  display: block;
  margin-left: 25px;
  float: right;
}

.mainimage {
  height: 100%;
  width: 100%;
  background-size: cover;
  position: relative;
  border-bottom: 6px dotted;

}

figure h2 { /*--got this code from https://www.w3schools.com/howto/howto_css_hero_image.asp--- sited in references*/
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 48px;
  font-family: 'Petit Formal Script', cursive;
}

figure p { /*--got this code from https://www.w3schools.com/howto/howto_css_hero_image.asp--- sited in references*/
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  font-family: 'Petit Formal Script', cursive;
}

/*--Sub-header--*/
.flexcontainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 80px;
  border-bottom: 6px dotted;
}

.flexcontainer img {
  height: 14%;
  width: 14%;
  margin: 42px;
  border-radius: 50%;
}

.flex1,
.flex2,
.flex3 {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 7vw;
}

.flex2 {
  color: rgb(241, 241, 232);
  text-decoration: underline;
}

/*--About--*/
#about {
  display: inline-flex;
  flex-direction: row;
  padding: 40px;
  border-bottom: 6px dotted black;
  justify-content: space-between;

}

#about h2 {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 130px;
  justify-content: center;
}

#about img {
  margin-top: 17%;
  height: 45%;
  width: 23%;
  border: 3px solid #1e1e1e;
  border-radius: 10%;
  justify-content: center;
}

#about p {
  border: 3px solid #1e1e1e;
  background-color: rgb(241, 241, 232);
  text-align: center;
  padding: 50px 6px;
  border-radius: 10%;
  height: 50%;
  margin-top: 17%;
}

.last-line {
  font-family: 'Petit Formal Script', cursive;
}

/*--Work--*/
#work {
  display: flex;
  align-items: center;
  padding: 10px;
  justify-content: space-between;
  border-bottom: 6px dotted;
}

#work h2 {
  float: left;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 83px;
  margin: 30px;
}

/* --Work Picture Grid--*/
/*--Got this grid code from https://www.freecodecamp.org/news/how-to-create-an-image-gallery-with-css-grid-e0f0fd666a5c/ sited in references--*/
.grid-container {
  width: 10%;
  margin: 2rem auto;
  margin-top: 40%;
  /* padding: 100px; */
  /* align-self: center; */
}

.gallery {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(5, 5vw);
  grid-gap: 1.5rem;
}

.gallery__img {
  width: 100%;
  height: 100%;
  background-size: cover;
  display: block;
}

.gallery__item--1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
}

.gallery__item--2 {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 3;
}

.gallery__item--3 {
  grid-column-start: 5;
  grid-column-end: 9;
  grid-row-start: 1;
  grid-row-end: 6;
}


.gallery__item--4 {
  grid-column-start: 1;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 6;
}

/*--Contact--*/
#contact {
  display: wrap;
  padding: 10px;
  scroll-margin-top: 200px;
}

.form {
  display: wrap;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  height: 400px;
}

input {
  display: flex;
  flex-direction: row;
  width: 50%;
  margin: 0 auto; 
  width:250px;
}

.form h2 {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 48px;
}

.message {
  height: 110px;
}

.contact-head {
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: space-between;
  float: left;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 85px;
  margin: 30px;
}

.contact-desc {
  font-size: 40px;
  text-align: center;
  font-family: 'Petit Formal Script', cursive;
  margin-left: 18%;
  margin-right: 80px;
  margin-top: 5%;
}

.contact-text {
  font-size: 20px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  ;
}

.contact-img img {
  height: auto;
  max-width: 50px;
}
.contact-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.contact-text {
  height: auto;
}


.resume h2 {
    display: flex;
    flex-direction: row;
    padding: 10px;
    justify-content: space-between;
    float: left;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 85px;
    margin: 30px;
}

.resume-screenshot, .proficiences {
  flex: 1;
  margin: 5%; /* You can adjust this to create more space between the image and the text */
  justify-content: space-evenly;
}

.resume p {
    display: flex;
    flex-direction: column;
    border: 3px solid #1e1e1e;
    background-color: rgb(241, 241, 232);
    text-align: center;
    padding: 50px 6px;
    border-radius: 10%;
}

.resume h3 {
  font-family: 'Petit Formal Script', cursive;
  font-size: 30px;
}

.resume li {
  list-style-type: none;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

#resume-container {
  display: inline;
  flex-direction: row;
  justify-content: space-between;
}

.resume img {
  margin-top: 6%;
  margin-left: 60px;
  width: 100%;
  height: 80%;
  position: static;
}

.resume {
  display: flex;
  justify-content: space-between; 
  align-items: center;
  padding: 30px;
  margin-top: 40px;
}

.download-resume {
    display: flex;
    font-size: 30px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    width: 65%;
    justify-content: center;
    position: absolute;
    bottom: 0;
    margin-bottom: 10%;
  }


@media (max-width: 900px) {

  body, figure h2,
  .contact-container, #contact, .contact-img,
  #work, #about, .flexcontainer, .contact-head,
  .contact-desc {
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin: 0;
      text-align: center;
  }

  @media (max-width: 900px) {
    .resume {
      flex-direction: column;
    }
  }

}
